/**
 * 游戏状态管理类
 * 统一管理游戏状态、历史记录、撤销重做等功能
 */
export default class GameStateManager {
  constructor(options = {}) {
    // 游戏状态标志
    this.hitflag = true; // 是否可以操作
    this.winflag = false; // 胜利标志
    this.loseflag = false; // 失败标志
    this.drawflag = false; // 平局标志

    // 历史记录
    this.history = [];

    // 自动模式相关
    this.isAutoRunning = false;
    this.autoInterval = null;
    this.autoStepDelay = options.autoStepDelay / 10 || 500; // 默认单步延迟500ms，测试时除以10以缩短测试时间
    this.maxSteps = options.maxSteps || 400; // 防止无限循环的最大步数，默认为400

    // 事件监听器
    this._listeners = {};
  }

  /**
   * 初始化游戏状态
   */
  init() {
    this.hitflag = true;
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;
    this.history = [];
    this.stopAuto();
    this.emit("init");
  }

  /**
   * 记录游戏操作到历史记录
   * @param {Object} operation - 操作对象
   */
  recordOperation(operation, backword = false) {
    backword ? this.history.unshift(operation) : this.history.push(operation);
    this.emit("historyUpdate", this.history);
  }

  /**
   * 撤销上一步操作
   * @returns {Object|null} 撤销的操作对象，如果没有历史记录则返回null
   */
  undo() {
    if (this.history.length === 0) {
      return null;
    }

    const operation = this.history.pop();
    this.emit("undo", operation);
    this.emit("historyUpdate", this.history);

    // 撤销后重置游戏状态标志
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;

    return operation;
  }

  /**
   * 获取历史记录长度
   * @returns {number} 历史记录长度
   */
  getStepCount() {
    return this.history.length;
  }

  /**
   * 判断是否可以撤销
   * @returns {boolean} 是否可以撤销
   */
  canUndo() {
    return this.history.length > 0 && this.hitflag && !this.isAutoRunning;
  }

  /**
   * 启动自动模式
   * @param {Function} stepCallback - 每一步的回调函数
   */
  async startAuto(stepCallback) {
    if (this.isAutoRunning || this.winflag || this.loseflag || this.drawflag) {
      console.log(
        `startAuto被跳过: isAutoRunning=${this.isAutoRunning}, win=${this.winflag}, lose=${this.loseflag}, draw=${this.drawflag}`,
      );
      return;
    }

    console.log("开始自动模式...");
    this.isAutoRunning = true;
    this.emit("autoStart");

    let stepCount = 0;

    try {
      while (
        this.isAutoRunning &&
        !this.overflag &&
        stepCount < this.maxSteps
      ) {
        await stepCallback();
        stepCount++;

        await this.wait();
      }

      if (stepCount >= this.maxSteps) {
        console.log(`达到最大步数${this.maxSteps}`);
        this.stopAuto();
      }
    } catch (error) {
      console.error("Auto mode error:", error);
    } finally {
      console.log(`自动模式结束，总共执行了${stepCount}步`);
      this.stopAuto();
    }
  }

  /**
   * 停止自动模式
   */
  stopAuto() {
    if (!this.isAutoRunning) {
      return;
    }

    this.isAutoRunning = false;
    if (this.autoInterval) {
      clearInterval(this.autoInterval);
      this.autoInterval = null;
    }

    this.emit("autoStop");
  }

  /**
   * 设置自动模式的延迟时间
   * @param {number} delay - 延迟时间（毫秒）
   */
  setAutoStepDelay(delay) {
    this.autoStepDelay = delay;
    this.emit("delayChanged", delay);
  }

  /**
   * 获取当前的延迟时间
   * @returns {number} 当前延迟时间（毫秒）
   */
  getAutoStepDelay() {
    return this.autoStepDelay;
  }

  /**
   * 等待指定时间，并在等待前触发事件
   * @param {number} delay - 延迟时间（毫秒），如果为null则使用autoStepDelay
   * @returns {Promise} 等待完成的Promise
   */
  async wait(delay = null) {
    const actualDelay = delay !== null ? delay : this.autoStepDelay;
    
    // 触发等待前事件
    this.emit("beforeWait", actualDelay);
    
    // 执行实际的等待
    return new Promise(resolve => setTimeout(resolve, actualDelay));
  }

  /**
   * 执行单步操作
   * @param {Function} stepCallback - 单步操作的回调函数
   */
  async step(stepCallback) {
    if (!this.hitflag || this.overflag) {
      return;
    }

    this.hitflag = false;
    try {
      await stepCallback();
    } catch (error) {
      console.error("Step error:", error);
    } finally {
      this.hitflag = true;
    }
  }

  /**
   * 设置游戏胜利
   */
  setWin() {
    this.winflag = true;
    this.stopAuto();
    this.emit("win");
  }

  /**
   * 设置游戏失败
   */
  setLose() {
    this.loseflag = true;
    this.stopAuto();
    this.emit("lose");
  }

  /**
   * 设置游戏平局
   */
  setDraw() {
    this.drawflag = true;
    this.stopAuto();
    this.emit("draw");
  }

  /**
   * 重置游戏
   * @param {Function} resetCallback - 重置游戏的回调函数
   */
  reset(resetCallback) {
    this.stopAuto();
    this.init();
    if (resetCallback) {
      resetCallback();
    }
    this.emit("reset");
  }

  /**
   * 添加事件监听器
   * @param {string} event - 事件名称
   * @param {Function} listener - 事件监听器
   */
  on(event, listener) {
    if (!this._listeners[event]) {
      this._listeners[event] = [];
    }
    this._listeners[event].push(listener);
  }

  /**
   * 移除事件监听器
   * @param {string} event - 事件名称
   * @param {Function} listener - 要移除的事件监听器
   */
  off(event, listener) {
    if (!this._listeners[event]) {
      return;
    }
    const index = this._listeners[event].indexOf(listener);
    if (index > -1) {
      this._listeners[event].splice(index, 1);
    }
  }

  /**
   * 触发事件
   * @param {string} event - 事件名称
   * @param {*} data - 事件数据
   */
  emit(event, data) {
    if (!this._listeners[event]) {
      return;
    }
    this._listeners[event].forEach((listener) => {
      try {
        listener(data);
      } catch (error) {
        console.error(`Error in event listener for ${event}:`, error);
      }
    });
  }

  /**
   * 获取当前游戏状态
   * @returns {Object} 游戏状态对象
   */
  getState() {
    return {
      hitflag: this.hitflag,
      winflag: this.winflag,
      loseflag: this.loseflag,
      drawflag: this.drawflag,
      step: this.getStepCount(),
      isAutoRunning: this.isAutoRunning,
    };
  }

  /**
   * 设置游戏状态
   * @param {Object} state - 要设置的游戏状态
   */
  setState(state) {
    Object.assign(this, state);
    this.emit("stateChange", this.getState());
  }

  /**
   * 获取默认计算属性对象
   * 供Vue组件使用扩展运算符导入
   * @returns {Object} 默认计算属性对象
   */
  static getDefaultComputedProperties() {
    return {
      // 基础状态标志
      hitflag() {
        const manager = this.gameManager;
        return manager ? manager.hitflag : true;
      },

      overflag() {
        const manager = this.gameManager;
        return manager ? manager.overflag : false;
      },

      winflag() {
        const manager = this.gameManager;
        return manager ? manager.winflag : false;
      },

      loseflag() {
        const manager = this.gameManager;
        return manager ? manager.loseflag : false;
      },

      drawflag() {
        const manager = this.gameManager;
        return manager ? manager.drawflag : false;
      },

      // 步数计算属性
      step() {
        const manager = this.gameManager;
        return manager ? manager.getStepCount() : 0;
      },

      // 按钮禁用状态计算属性
      undoDisabled() {
        const manager = this.gameManager;
        return !manager || !manager.canUndo();
      },

      restartDisabled() {
        const manager = this.gameManager;
        return !manager || !manager.hitflag || manager.isAutoRunning;
      },

      stepDisabled() {
        const manager = this.gameManager;
        return (
          !manager ||
          !manager.hitflag ||
          manager.isAutoRunning ||
          manager.overflag
        );
      },

      autoDisabled() {
        const manager = this.gameManager;
        return (
          !manager ||
          !manager.hitflag ||
          manager.overflag
        );
      },

      // 用于模板的便捷计算属性
      canOperate() {
        const manager = this.gameManager;
        return manager && manager.hitflag && !manager.isAutoRunning;
      },

      // 游戏状态
      gameState() {
        const manager = this.gameManager;
        return manager ? manager.getState() : {};
      },
    };
  }
}
